@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300&display=swap);
.body {
    background: #343130;
}
.home {
    display : flex;
    flex-direction: column;
    justify-content: flex-start;
    flex-wrap : nowrap;
    text-decoration: none;
    outline: none;
}

.home-navBar {
    text-decoration: none;
    outline: none;
    order : 1;
}

.navBar {
    display : flex;
    flex-direction: row;
    justify-content: space-between;
    height : 6vh;
    width : 100%;
    margin-top : 3vh;
    text-decoration: none;
    outline: none;
}

.navBar-logo {
    color: #D3D5D6;
    letter-spacing : 0.07rem;
    text-decoration : none;
    outline : none;
    transition : all 0.2s;
    padding : 0.4rem 0.9rem;
    font-size: 5vh;
    font-family: 'Montserrat', sans-serif;
    font-weight: bold;
}

.a {
    text-decoration: none;
    outline: none;
}


.button{
    margin-left : 2vh;
    margin-right : 2vh;
    height : 7vh;
    font-weight : 600;
    color : #F6F8FA;
    width : 25vh;
    background-color :#F36D1A;
    cursor : pointer;
    border-radius : 1rem;
    display : flex;
    flex-direction : row;
    flex-wrap : wrap;
    justify-content : center;
    align-items: center;
    align-content: center;
    transition : all 0.2s;
    text-decoration: none;
    outline: none;
}

.button-logo{
    margin-right: 0.5rem;
    height : 4vh;
    width : 4vw;
    color : #D3D5D6;
    text-decoration: none;
    outline: none;
}

.button-text{
    margin-right: 0.5rem;
    color : #D3D5D6;
    font-family: 'Montserrat', sans-serif;
    font-weight: bold;
    text-decoration: none;
    outline: none;
}

.bodyText {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    align-content: flex-start;
    margin-top: 4rem;
    margin-left : 1.5rem;
}

.bodyText-div-head {
    display:flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    align-content: flex-start;
    padding-bottom: 0rem;
}

.bodyText-head-online {
    margin : 0px;
    padding: 0px;
    color: #D3D5D6;
    letter-spacing : 0.07rem;
    text-decoration : none;
    outline : none;
    transition : all 0.2s;
    font-size: 10vh;
    font-family: 'Montserrat', sans-serif;
    font-weight: lighter;
}

.bodyText-head-quiz {
    margin: 0px;
    padding: 0px;
    color: #D3D5D6;
    letter-spacing : 0.07rem;
    text-decoration : none;
    outline : none;
    transition : all 0.2s;
    font-size: 10vh;
    font-family: 'Montserrat', sans-serif;
    font-weight: lighter;
}


.underLine {
    margin-left:0.4rem;
    border : none;
    display : block;
    width : 50%;
    background-color : #D3D5D6;
    height : 1px;
}

.bodyText-byLine {
    margin-top: 7px;
    margin-left : 0.5rem;
    color: #D3D5D6;
    letter-spacing : 0.07rem;
    text-decoration : none;
    outline : none;
    transition : all 0.2s;
    font-size: 3vh;
    font-family: 'Montserrat', sans-serif;
    font-weight: normal;
}

.home-signInButtonLarge {
    margin: 2rem 2rem 4rem 4rem;
}


.play {
    display : flex;
    flex-direction : column;
    justify-content: space-between;
    margin : 0px;
}

.navBar {
    display : flex;
    flex-direction: row;
    justify-content: space-between;
    align-items : center;
    height : 6vh;
    width : 100%;
    margin-top : 0vh;
    text-decoration: none;
    outline: none;
}

.salvos-logo {
    color: #F36D1A;
    letter-spacing : 0.07rem;
    text-decoration : none;
    outline : none;
    transition : all 0.2s;
    padding : 0.2rem 0.6rem;
    font-size: 4vh;
    font-family: 'Montserrat', sans-serif;
    font-weight: bold;
}

.helloName {
    text-align: right;
    color: #D3D5D6;
    letter-spacing : 0.04rem;
    text-decoration : none;
    outline : none;
    transition : all 0.2s;
    padding : 0.4rem 0.9rem;
    font-size: 3vh;
    width : 50vh;
    font-family: 'Montserrat', sans-serif;
    font-weight: bold;
}

.dynamicGameArea {

}





@media only screen and (min-width: 768px) {
    .gameArea {
        margin : 10vh 0vh;
        height : 60vh;
        width : 100%;
        display : flex;
        flex-direction : column;
        justify-content: center;
        align-items: center;
    }

    .start-button {
        margin: 6vh 2vh;
    }
    .start-text {
        margin-top : 0vh;
        margin-bottom: 2vh;
        padding: 0px;
        display: block;
        color: #D3D5D6;
        letter-spacing : 0.07rem;
        text-decoration : none;
        outline : none;
        transition : all 0.2s;
        font-size: 10vh;
        font-family: 'Montserrat', sans-serif;
        font-weight: lighter;
    }
    .start-underLine{
        margin-left:0.4rem;
        border : none;
        display : block;
        width : 50%;
        background-color : #D3D5D6;
        height : 1px;
    }

}


@media only screen and (max-width: 768px) {
    .gameArea {
        margin : 0vh 0vh;
        height : 80vh;
        width : 100%;
        display : flex;
        flex-direction : column;
        justify-content: center;
        align-items: center;
    }

    .start-button {
        margin: 6vh 2vh;
    }

    .start-text {
        align-self: center;
        margin-top : -3rem;
        margin-bottom : 0px;
        padding : 0px;
        display: block;
        color: #D3D5D6;
        letter-spacing : 0.07rem;
        text-decoration : none;
        outline : none;
        transition : all 0.2s;
        font-size: 10vh;
        font-family: 'Montserrat', sans-serif;
        font-weight: lighter;
    }
    .start-underLine{
        align-self: flex-start;
        margin-top: 2vh;
        margin-bottom: 3vh;
        border : none;
        display : block;
        width : 40%;
        background-color : #D3D5D6;
        height : 1px;
    }
}




/*mobile*/
@media only screen and (max-width: 768px) {
    .gameArea-question-render {
        margin : 0vh 0vh;
        height : 80vh;
        width : 100%;
        display : flex;
        flex-direction : column;
        justify-content: center;
        align-items: center;
    }
}

/*PC*/
@media only screen and (min-width: 768px) {
    .gameArea-question-render {
        margin: 10vh 0vh;
        height: 60vh;
        width: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
}

.gameScreen {
    margin-top: 3rem;
    height : 100%;
    width : 100%
}

.gameRenderer {
    display : flex;
    flex-direction : column;
    justify-content: flex-start;
    flex-wrap: wrap;
}

.questionHead {
    display : flex;
    flex-direction: row;
    justify-content: space-between;
}

.questionSubHead {
    order : 1;
    display : flex;
    flex-direction: row;
    justify-content: flex-start;
}

.questionHeadText {
    margin-right: 2vw;
    display: inline;
    order : 1;
    color: #d3d5d6;
    font-family: "Montserrat", sans-serif;
    font-size: medium;
    letter-spacing: 0.04rem;
    align-self: flex-start;
}

.counterContainer {
    margin-left: 2vw;
    order : 2;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
}

.questionCounter {
    order : 1;
    display: inline;
    font-weight: bold;
    font-size: large;
    font-family: "Montserrat", sans-serif;
    color: #d3d5d6;
    letter-spacing: 0.07rem;
}

.counterUnderLine {
    order : 2;
    align-self: flex-start;
    border : none;
    display : block;
    width : 50%;
    background-color : #F36D1A;
    height : 1px;
}

.timerContainer {
    margin: -5vh 5vw 0vh 0vw;
    order : 2;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
}

.timerHead {
    color: #d3d5d6;
    font-weight: lighter;
    font-family: "Montserrat", sans-serif;
}

.timer {
    align-self: center;
    color: #d3d5d6;
    font-weight: bold;
    text-decoration: none;
    font-size: 10vh;
    font-family: 'Montserrat', sans-serif;
}

.timerUnderLine {
    align-self: flex-start;
    margin-left:0.4rem;
    border : none;
    display : block;
    width : 50%;
    background-color : #F36D1A;
    height : 1px;
}



.question {
    margin : 8vh 0vw 3vh 0vw;
    font-family: "Montserrat", sans-serif;
    font-weight: bold;
    letter-spacing: 0.02rem;
    color: #d3d5d6;
    font-size: medium;
}

.questionImage {

}

.optionsContainer {
    margin-top: 2rem;
    padding-right: 0px;
    width: 100vw;
    margin-left: -3vh;
}

.options {
    list-style : none;
    display : flex;
    flex-direction : column;
    justify-content: flex-start;
    align-items: flex-start;
}

.optionInstant {
    align-self: flex-start;
    width: 100%;
}

.optionButton {
    text-align: center;
    border-radius: 0px;
    border-bottom-style: solid;
    border-bottom-color: #F36D1A;
    border-right-style: none;
    height : 10vh;
    width : 90%;
    font-size: medium;
    font-weight: bold;
    font-family : "Montserrat", sans-serif;
    outline : none;
    text-decoration: none;
    background-color: #d3d5d6;
    color: #343130;
    transition: all 0.2s;
}

@media only screen and (min-width: 768px){

    .optionsContainer {
        padding-right: 0px;
        width: 50vw;
        margin-left: 20vw;
    }

    .options {
        list-style : none;
        display : flex;
        flex-direction : column;
        justify-content: flex-start;
        align-items: flex-start;
    }

    .optionInstant {
        align-self: flex-start;
        width: 100%;
    }

    .optionButton {
        text-align: center;
        border-radius: 0px;
        border-bottom-style: solid;
        border-bottom-color: #F36D1A;
        border-right-style: none;
        height : 10vh;
        width : 90%;
        font-size: medium;
        font-weight: bold;
        font-family : "Montserrat", sans-serif;
        outline : none;
        text-decoration: none;
        background-color: #d3d5d6;
        color: #343130;
        transition: all 0.2s;
    }
}

.result {
    height: 100%;
    width: 100%;
    background-color: #D3D5D6;
    color: #343130;
    font-size: medium;
    font-family: "Montserrat", sans-serif;
}











